body {
    background-color: rgb(175, 235, 255);
    text-align: center;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.signUp {
    display: flex;
    flex-direction: column;
    text-align: left;
    margin: auto;
    width: 90%;
    max-width: 400px;
    padding: 20px;
    border: 2px solid grey;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

.signUp label, 
.signUp input {
    width: 100%;
    margin: 5px 0;
}

.profile {
    display: flex;
    flex-direction: column;
}

.button {
    margin-top: 5px;
    cursor: pointer;
}

.submit {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.submit_button {
    background-color: royalblue;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
}

